@import "./reset.scss";

$bg: rgb(255, 246, 224);
$bg-font: rgb(255, 228, 158);
$primary: rgb(255, 176, 57);
$black: rgb(43, 43, 43);
$white: rgb(255, 255, 255);

:root {
    font-family: 'Courier New', Courier, monospace;
    color: $black;

    button {
        border: 0px;
        padding: 10px 20px;
        border-radius: 10px;
        cursor: pointer;
    }
}

.animation-fade {
    animation: animationFade ease 1s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

@keyframes animationFade {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.animation-slide-out-left {
    animation: slide-out-left normal 1s;
}

@keyframes slide-out-left {
    from {
        margin-right: 0%;
    }

    to {
        margin-right: 1000%;
    }
}

.animation-slide-in-left {
    animation: slide-in-left normal 0.5s;
}

@keyframes slide-in-left {
    from {
        margin-right: -100%;
    }

    to {
        margin-right: 0%;
    }
}

.animation-slide-out-right {
    animation: slide-out-right normal 1s;
}

@keyframes slide-out-right {
    from {
        margin-left: 0%;
    }

    to {
        margin-left: 1000%;
    }
}

.animation-slide-in-right {
    animation: slide-in-right normal 0.5s;
}

@keyframes slide-in-right {
    from {
        margin-left: -100%;
    }

    to {
        margin-left: 0%;
    }
}

body {
    background-color: $bg;
}

img {

    &.web,
    &.mobile {
        border: 1.5vh solid $black;
        border-radius: 1.5vh;
    }
}

.app {

    overflow-x: hidden;
    position: relative;

    >div {
        height: 100vh;
        position: relative;
    }

    .menu {
        z-index: 1;
        position: absolute;
        height: fit-content;
        width: 100%;
        display: flex;
        justify-content: flex-end;

        .language-list {
            margin: 10px;
            background-color: $primary;
            width: fit-content;
            border: 2px solid $primary !important;
            border-radius: 10px;

            .language {
                border-radius: 0px;
                gap: 5px;
                background-color: $bg;

                &.active {
                    background-color: $primary;
                    color: white;
                }

                &:first-child {
                    border-top-left-radius: 10px;
                    border-bottom-left-radius: 10px;
                }

                &:last-child {
                    border-top-right-radius: 10px;
                    border-bottom-right-radius: 10px;
                }
            }
        }
    }

    .watermark {
        z-index: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0.5;
        color: $bg-font;
        font-size: 10vw;
    }

    .main {
        display: flex;
        align-items: center;
        justify-content: center;

        .container {
            width: 80vw;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;

            >div {
                margin: 0px auto;
                padding: 20px;
                transition: transform 3s ease;
            }

            .left {
                button {
                    border: 2px solid $primary !important;
                    background-color: $bg;
                    display: flex;
                    align-items: center;
                    gap: 10px;

                    img {
                        height: 21px;
                        width: 21px;
                    }
                }
            }

            .right {}
        }
    }

    .skills {
        display: flex;
        align-items: center;
        justify-content: center;

        .container {
            z-index: 1;
            width: 80vw;
            display: flex;
            align-items: center;
            justify-content: center;
            shape-outside: radial-gradient(farthest-side at right, transparent calc(100% - var(--p)), #fff 0);


            .skills {
                width: 50vw;
                display: flex;
                flex-wrap: wrap;

                span {
                    margin: 10px;
                }
            }
        }
    }

    .portfolio {

        .container {
            .item {
                z-index: 1;
                height: 100vh;
                display: flex;
                flex-direction: column;

                .header {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;

                    .container {
                        width: 90vw;
                    }
                }

                .body {
                    display: flex;
                    flex-grow: 1;
                    padding: 0px 10px;

                    .left {
                        cursor: pointer;
                        display: flex;
                        align-items: center;
                    }

                    .content {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex-grow: 1;

                        img {
                            max-height: 65vh;
                            max-width: 70vw;
                            object-fit: contain;
                        }
                    }

                    .right {
                        cursor: pointer;
                        display: flex;
                        align-items: center;
                    }
                }

                .footer {
                    display: flex;
                    align-items: center;
                    flex-direction: column;

                    button {
                        background-color: $primary;
                        color: $white;
                    }

                    .nav {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        justify-content: center;

                        .dot {
                            background-color: $bg-font;
                            height: 10px;
                            width: 10px;
                            margin: 10px;
                            border-radius: 50%;

                            &.active {
                                background-color: $primary;
                            }
                        }
                    }
                }

                .disabled {
                    opacity: 0.1;
                }
            }
        }
    }

    .portfolio-detail {
        display: flex;
        justify-content: center;

        .container {
            width: 80vw;
            padding-top: 20px;

            .images {
                display: flex;
                flex-flow: row wrap;
                justify-content: center;
                gap: 10px;

                .image {
                    cursor: pointer;

                    img {
                        max-width: 350px;
                        max-height: 25vh;
                        object-fit: contain;
                    }
                }
            }
        }
    }
}

.image-preview {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    height: 100vh;
    width: 100vw;
    background-color: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(10px);

    .close {
        position: absolute;
        height: 20px;
        width: 20px;
        top: 10px;
        right: 10px;
        cursor: pointer;
    }

    img {
        max-width: 85vw;
        max-height: 85vh;
    }
}